@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  min-width: 320px;
  height: 100%;
  margin: 0 auto;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page {
    min-width: 768px;
  }
}
@media (min-width: 1440px) {
  .page {
    min-width: 1440px;
  }
}

.page__body {
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 16px;
  color: #000000;
  background-color: #ffffff;
  margin: 0 auto;
  min-height: 100%;
  position: relative;
  z-index: -2;
}

.page__footer {
  position: relative;
}

.button {
  cursor: pointer;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #68b738;
  text-align: center;
}
.button:focus {
  outline: 1px solid #5eaa2f;
  outline-offset: 1px;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
  display: block;
}

.header {
  box-sizing: border-box;
  background-color: #ffffff;
  min-width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header {
    min-width: 768px;
  }
}
@media (min-width: 1440px) {
  .header {
    min-width: 1440px;
  }
}

.header__wrapper {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 320px;
  padding: 0 20px;
  min-height: 66px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__wrapper {
    width: 768px;
    padding: 25px 0 0 0;
    min-height: 80px;
    justify-content: space-around;
  }
}
@media (min-width: 1440px) {
  .header__wrapper {
    width: 1440px;
    min-height: 150px;
    padding: 15px 110px 0 110px;
  }
}

.header__wrapper--inner {
  margin-bottom: 27px;
}
.header__wrapper--inner::after {
  content: "";
  display: block;
  width: 1000%;
  position: absolute;
  top: 66px;
  left: 0;
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .header__wrapper--inner::after {
    display: none;
  }
}
@media (min-width: 768px) {
  .header__wrapper--inner {
    margin: 0 auto;
    padding: 25px 30px;
    justify-content: flex-start;
  }
}
@media (min-width: 1440px) {
  .header__wrapper--inner {
    background: linear-gradient(to right, #ffffff 50%, #ffffff 100%);
    padding: 55px 110px 30px 110px;
  }
}

.header__logo-image {
  z-index: 2;
}
@media (min-width: 768px) {
  .header__logo-image {
    margin-right: 94px;
  }
}
@media (min-width: 1440px) {
  .header__logo-image {
    margin-right: 24px;
  }
}

@media (min-width: 768px) {
  .header__logo-text {
    display: none;
  }
}

@media (min-width: 768px) {
  .header__logo-img {
    width: 174px;
    height: 52px;
  }
}
@media (min-width: 1440px) {
  .header__logo-img {
    width: 202px;
    height: 59px;
  }
}

.header__logo-img:hover {
  opacity: 0.8;
}

.header__logo-img:active {
  opacity: 0.6;
}

.navigation {
  position: relative;
  z-index: 2;
}
@media (min-width: 768px) {
  .navigation {
    display: flex;
  }
}
@media (min-width: 1440px) {
  .navigation {
    margin-left: auto;
  }
}

.navigation__toggle--open {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 24px;
  min-height: 20px;
  border: none;
  background-color: transparent;
  border-top: 2px solid #000000;
  cursor: pointer;
}
.navigation__toggle--open::after {
  content: "";
  display: block;
  position: absolute;
  top: 7px;
  min-width: 24px;
  border-bottom: 2px solid #000000;
}
.navigation__toggle--open::before {
  content: "";
  position: absolute;
  top: 15px;
  display: block;
  min-width: 24px;
  border-top: 2px solid #000000;
}
@media (min-width: 768px) {
  .navigation__toggle--open {
    display: none;
  }
}

.navigation__toggle--close {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 24px;
  min-height: 20px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.navigation__toggle--close::after {
  content: "";
  display: block;
  min-width: 24px;
  border-bottom: 1.5px solid #000000;
  transform: rotate(-45deg);
}
.navigation__toggle--close::before {
  content: "";
  position: absolute;
  top: 9px;
  display: block;
  min-width: 24px;
  border-top: 1.5px solid #000000;
  transform: rotate(45deg);
}
@media (min-width: 768px) {
  .navigation__toggle--close {
    display: none;
  }
}

.navigation__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
@media (max-width: 767px) {
  .navigation__list {
    position: absolute;
    left: -276px;
    top: 45px;
    width: 320px;
    background-color: #ffffff;
  }
}
@media (min-width: 768px) {
  .navigation__list {
    position: static;
    display: flex;
    width: 440px;
    justify-content: flex-end;
  }
}
@media (min-width: 1440px) {
  .navigation__list {
    display: flex;
    width: 640px;
  }
}

@media (max-width: 767px) {
  .navigation__item {
    box-sizing: border-box;
    display: flex;
    min-height: 66px;
    justify-content: center;
    align-items: center;
    margin: 0;
    border-top: 1px solid #e6e6e6;
  }
  .navigation__item:last-child {
    border-bottom: 1px solid #e6e6e6;
  }
}
@media (min-width: 768px) {
  .navigation__item:not(:last-child) {
    margin-right: 34px;
  }
}

.navigation__link {
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
}
@media (max-width: 767px) {
  .navigation__link {
    padding: 22px;
  }
}
@media (min-width: 768px) {
  .navigation__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    padding: 8px 0;
  }
  .navigation__link:hover {
    color: rgba(0, 0, 0, 0.5);
  }
  .navigation__link:active {
    color: rgba(0, 0, 0, 0.3);
  }
}
@media (min-width: 1440px) {
  .navigation__link {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: #ffffff;
  }
  .navigation__link:hover {
    color: rgba(255, 255, 255, 0.5);
  }
  .navigation__link:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

@media (min-width: 768px) {
  .navigation__link--current {
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1440px) {
  .navigation__link--current {
    border-bottom: 2px solid #ffffff;
  }
}

@media (min-width: 1440px) {
  .navigation__link--inner-current {
    border-bottom: 2px solid #68b738;
  }
}

@media (max-width: 767px) {
  .navigation--closed .navigation__list {
    display: none;
  }

  .navigation--opened .navigation__list,
.navigation--nojs .navigation__list {
    display: block;
  }
}
.navigation__link--inner {
  color: #000000;
}
@media (min-width: 1440px) {
  .navigation__link--inner {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    padding: 8px 0;
  }
