  .advantages__wrapper::after {
    content: "";
    display: block;
    width: 768px;
    border-bottom: 1px solid #e6e6e6;
    position: relative;
    right: 30px;
    top: 25px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .advantages__wrapper::after {
    display: none;
  }
}
@media (min-width: 1440px) {
  .advantages__wrapper {
    width: 1440px;
    padding: 25px 110px 20px 110px;
  }
}

.advantages__title {
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  margin: 0 0 40px 0;
}
@media (min-width: 768px) {
