    margin: 0 0 32px 0;
    font-size: 60px;
    line-height: 60px;
  }
}

.advantages__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  counter-reset: program;
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: 250px 250px;
    gap: 18px 135px;
  }
}
@media (min-width: 1440px) {
  .advantages__list {
    display: flex;
    gap: 0;
  }
}

.advantages__item {
  box-sizing: border-box;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  width: 280px;
  min-height: 60px;
  padding: 2px 0 0 80px;
  background-repeat: no-repeat;
  background-position: 0 0;
  counter-increment: program;
  position: relative;
}
.advantages__item:not(:last-child) {
  margin-bottom: 20px;
}
@media (min-width: 1440px) {
  .advantages__item:not(:last-child) {
    margin-bottom: 0;
    margin-right: 80px;
  }
}
@media (min-width: 768px) {
  .advantages__item {
    font-size: 16px;
    line-height: 24px;
    max-width: 245px;
    min-height: 280px;
    padding: 151px 0 57px 0;
  }
  .advantages__item::after {
    content: counter(program);
    width: 136px;
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    font-size: 280px;
    line-height: 280px;
    color: #f2f2f2;
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
  }
}

.advantages__item--usefully {
  background-image: url("../img/green-background-1.svg");
}
@media (min-width: 768px) {
