.main-welcome {
  box-sizing: border-box;
  background: linear-gradient(#68b738 30%, #ffffff 40%, #ffffff 100%);
  background-image: url("../img/background-main.jpg");
  box-shadow: inset 0 302px 0 0 rgba(104, 183, 56, 0.85);
  background-position: top;
  background-repeat: repeat-x;
  margin: 0 auto;
  min-width: 320px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/background-main@2x.jpg");
    background-size: 320px auto;
  }

  @media (min-width: $tablet-width) {
    min-width: 768px;
    background-image: none;
    box-shadow: none;
  }

  @media (min-width: $desktop-width) {
    min-width: 1440px;
    background-image: url("../img/img-desktop/background-main.jpg");
    background-repeat: no-repeat;
    background-position: 100% 0;
    margin-top: -150px;

    &::before {
      content: "";
      display: block;
      position: absolute;
      width: 720px;
      min-height: 694px;
      background-color: rgba(104, 183, 56, 0.85);
      left: calc(100vw - 720px);
      top: 0;
    }

    &::after {
      content: "";
      display: block;
      position: absolute;
      width: calc(50vw - 720px);
      min-height: 694px;
      background-color: #64a83c;
      left: 50%;
      top: 0;
    }

    @media (min-resolution: 2dppx) {
      background-image: url("../img/img-desktop/background-main@2x.jpg");
      background-size: 720px auto;
    }
  }
}

.main-welcome__wrapper {
  box-sizing: border-box;
  position: relative;
  margin: 0 auto;
  width: 320px;
  padding: 27px 20px 10px 20px;

  @media (min-width: $tablet-width) {
    width: 768px;
    min-height: 850px;
    padding: 62px 237px 10px 117px;
  }

  @media (min-width: $desktop-width) {
    width: 1440px;
    min-height: 694px;
    padding: 170px 110px 0 110px;
  }
}

.main-welcome__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: #ffffff;
  width: 250px;
  margin: 0 0 25px 16px;
  padding: 0;
  text-align: center;

  @media (min-width: $tablet-width) {
    font-weight: 400;
    font-size: 60px;
    line-height: 60px;
    color: #000000;
    width: 416px;
    margin: 0 0 41px 0;
    text-align: left;
  }

  @media (min-width: $desktop-width) {
    margin: 53px 0 40px 80px;
  }
}

.main-welcome__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  width: 198px;
  margin: 0 0 29px 40px;

  @media (min-width: $tablet-width) {
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    color: #000000;
    width: 416px;
    margin: 0 0 35px 3px;
    text-align: left;
  }

  @media (min-width: $desktop-width) {
    margin: 0 0 52px 80px;
  }
}

.main-welcome__button {
  box-sizing: border-box;
  display: inline-block;
  background-color: $basicgreen;
  width: 280px;
  padding: 11px 44px;
  color: #ffffff;

  @media (max-width: $mobile-width-only) {
    margin: 12px 0 0 0;
  }

  @media (min-width: $tablet-width) {
    max-width: 244px;
    padding: 14px 26px;
    margin: 12px 0 0 4px;
  }

  @media (min-width: $desktop-width) {
    margin: 0 0 0 80px;
  }
}

.main-welcome__button-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-decoration: none;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 26px;
  }
}

.main-welcome__image {
  @media (min-width: $tablet-width) {
    position: absolute;
    z-index: -1;
    left: 30px;
    top: 260px;
    width: 709px;
    height: 609px;
  }

  @media (min-width: $desktop-width) {
    width: 552px;
    height: 499px;
    position: absolute;
    z-index: 1;
    left: 560px;
    top: 140px;
  }
}
